import React from "react";
import AuthorDetails from "./AuthorDetails";
import Media from "./Media";
import PostContent from '../../Elements/PostContent/PostContent'
import CTAButton from "../../Elements/CTA/CtaButton";
import Rating from "../../Elements/Rating/Rating";
import WidgetShare from "./WidgetShare";
import WidgetSocialActions from "./WidgetSocialActions";
import { PostTrimContent } from "../../../../utils";
import ShareOnHover from "../../Elements/ShareOnHover/ShareOnHover";
import { isTagbox } from "../../../../actions/api";

const trim_content_length = 200;
const Card = ({ itemData, personalization, ThemeRule, wallID, clickToShowPopUp, itemIndex, ownerId, onClickToCTA, wall, ThemeInfo }) => {

  const ThemeId = ThemeInfo.id
  const mediaType = !([53,57,85].includes(ThemeId) && isTagbox) && itemData.type !== 1 ? true : false;
  const rating = (itemData.rating > 0) ? true : false;
  const contentClass = (itemData.rating > 0) ? 'tb_wt_rating_content' : 'tb_wt_content'
  const textDecorate = (itemData.type === 1 && personalization.textDecorate) ? 'tb_wt_text_decoration tb_wt_text_post' : '';
 
  const postTextContent = personalization.trimcontent ? PostTrimContent(itemData.content, trim_content_length) : itemData.content
  return <div id={`tb-wt-post-${itemData.id}`} className={`tb_wt_post_wrapper`} tb-network={itemData.network.id} tb-product={itemData.isShopIcon ? 1 : itemData.isHotspot ? 2 : 0} role="article" aria-label={`Post ${parseInt(itemIndex) + 1},${postTextContent}`} tabIndex="0">
    <div className="tb_wt_post_in" onClick={clickToShowPopUp(itemIndex, itemData)} role="button" aria-label="Popup" tabIndex={"0"}>
      <AuthorDetails ownerId={ownerId} postTime={itemData.createdAt} author={itemData.author} network={itemData.network} font={ThemeRule} personalization={personalization} ThemeRule={ThemeRule} mediaType={mediaType} />
      <div className="tb_wt_post_media_wrapp" role="button" tabIndex="0" aria-label="Pop Up Button">
        {mediaType ? <Media key={`img${itemIndex}_${itemData.id}`} itemData={itemData} wallID={wallID} ownerId={ownerId} wall={wall} /> : ''}
        {rating ? <div className="tb_wt_rating__"><Rating rating={itemData.rating} network={itemData.network} /> </div> : ''}
      </div>
      <div className="tb_wt_contant_wrapper">
        {itemData.share.status ? <ShareOnHover share={itemData.share} shareClass={'tb_wt_share_container'} ThemeInfo={ThemeInfo} Post={itemData} /> : ''}
        { itemData.ctaStatus ? <div className="tb_wt_post_cta"><CTAButton ctaClass={'tb_wt_post_cta_btn'} cta={itemData.cta} item={itemData} onClickToCTA={onClickToCTA} /> </div> : ''}
        {!ThemeRule.hideContent || itemData.type == 1 ? <PostContent contentClass={`${contentClass} ${textDecorate}`} item={itemData} content={postTextContent} font={ThemeRule} ThemeRule={ThemeRule} personalization={personalization} contentTitle={itemData.contentTitle} /> : ''}
        <div className="tb_wt_post_share_container" style={((itemData && itemData.network.id == 29) || !ThemeRule.socialAction) ? { justifyContent: `flex-end` } : {}}>
          {ThemeRule.socialAction ? <WidgetSocialActions itemData={itemData} ThemeRule={ThemeRule} /> : ''}
          {itemData.share.status ? <WidgetShare share={itemData.share} shareClass={'tb_mc_share_container'} /> : ''}
        </div>
      </div>
    </div>
  </div>
}
export default Card