import { isTagbox } from "../../../../actions/api";
import MediaIcon from "../../Elements/MediaIcon/MediaIcon";
import Image from "../../Elements/Media/Image";
import Video from "../../Elements/Media/Video";
const Media = ({ itemData, wallID, ownerId, wall }) => {
  const autoPlay = [3,4].includes(itemData.type) && wall.ThemeRule.autoPlay == 1 ? true : false
  const size = isTagbox && wall.ThemeRule.aspectImageRatio || false

  return <div className="tb_wt_media_wrap" role="button" tabIndex="0" aria-label="Post Pop up media wrapper">
    <MediaIcon itemData={itemData} IconClass="tb_wt_media_icon" show={true}  />
    {autoPlay ?
      <Video VideoClass="tb_wt_video" data={itemData} wallID={wallID} themeID={wall?.ThemeInfo.id} ownerId={ownerId} size={size} isCover={true} controls={false} autoPlay={false} muted={true} observer={true} />
      :
      <Image ImageClass="tb_wt_image" data={itemData} wallID={wallID} themeID={wall?.ThemeInfo.id} ownerId={ownerId} size={size} ThemeRule={wall.ThemeRule} observer={true} />
    }
  </div>
}
export default Media