import Card from "./Elements/Card";
import './scss/WidgetTheme.scss'
const WidgetTheme = ({ postData, completeDataObject, adjustWidth, CardStyle, ThemeStyle, ProductSetting, ThemeInfo, clickToShowPopUp, onClickToCTA }) => {
  return <div className="tb_wt_post_container">
    {
      ((postData && postData.length > 0)) && postData.map((item, index) => {
        const cardData = completeDataObject[item];
        return <Card ownerId={ThemeInfo.ownerId} itemData={cardData} key={`wdt_crd_${index}_${item.id}`} itemIndex={index} adjustWidth={adjustWidth} CardStyle={CardStyle} ThemeStyle={ThemeStyle} ProductSetting={ProductSetting} wallID={ThemeInfo.wallId} clickToShowPopUp={clickToShowPopUp} onClickToCTA={onClickToCTA} />
      })
    }
  </div>
}
export default WidgetTheme
