import Author from "../../Elements/Author/Author";
import Time from "../../Elements/Time/Time";
import Network from "../../Elements/Network/Network";
import Seprator from "../../Elements/Seprator/Seprator";
const AuthorDetails = ({ author, personalization, postTime, network, ThemeRule }) => {
  const username = author.username ? author.username.length > 0 ? `@${author.username}` : `` : '';
  const postAuthor = (personalization.postAuthor && !author.isInstaUser) ? true : false;
  const authorSeprator = (personalization.postAuthor && personalization.postTime && !author.isInstaUser) ? true : false;
  return <div className="tb_wt_author_wrapper" aria-label="Author details" aria-roledescription="post author details">
    <div className="tb_wt_author">
      {postAuthor ? <Author author={author} network={network} authorClass={'tb_wt_author_profile'} /> : ''}
      <div className="tb_wt_author_info">
        {postAuthor ? <div className="tb_wt_authorname">{author.name}</div> : ''}
        <div className="tb_wt_post_info">
          {postAuthor ? <div className="tb_wt_username">{username}</div> : ''}
          {authorSeprator ? <Seprator sepClass={`tb_wt_seprator`} /> : ''}
          {personalization.postTime ? <Time postTime={postTime} timeClass={`tb_wt_time tb-cTBfont-${ThemeRule.font_varient}`} authorColor={ThemeRule.authorColor} /> : ''}
        </div>
      </div>
    </div>
    <div className="tb_wt_social_">
      <Network networkClass={'tb_wt_social_ico'} network={network} isDefault={ThemeRule.iconType === 1 ? true : false} ThemeRule={ThemeRule} />
    </div>
  </div>
}
export default AuthorDetails