import { convertToAtob } from "../../../../utils/Common"

const NetworkLike = (networkID, postID, Link) => {
  switch (networkID) {
    case 1:
      return `https://twitter.com/intent/favorite?tweet_id=${convertToAtob(postID)}`
    default:
      return Link
  }
}
const NetworkComment = (networkID, postID, Link) => {
  switch (networkID) {
    case 1:
      return `https://twitter.com/intent/tweet?in_reply_to=${convertToAtob(postID)}`
    default:
      return Link
  }
}
const WidgetSocialActions = ({ itemData, ThemeRule }) => {
  const networkID = itemData.network.id
  const likeIcon = (networkID === 3 || networkID === 10) ? 'like' : 'heart-outline';

  return <>
    {(networkID !== 12 && networkID !== 15 && networkID !== 20 && networkID !== 21 && networkID !== 29) ?
      <div className="tb_wt_social_action__" role="navigation">
        <div className="tb_wt_social_action__list">
          <a href={NetworkLike(networkID, itemData.postId, itemData.link)} target="_blank" className="tb_wt_social_action__ico_wrap" rel="noopener noreferrer nofollow" aria-label={`Post Like Url ${NetworkLike(networkID, itemData.postId, itemData.link)}`} title="Favorite">
            <div className={`tb_wt_social_action_ico__ tb__icon tb-${likeIcon}`}> </div>
            {itemData.like_count > 0 ? <div className="tb_wt_social_action_counts__">{itemData.like_count}</div> : ''}
          </a>
        </div>
        <div className="tb_wt_social_action__list">
          <a href={NetworkComment(networkID, itemData.postId, itemData.link)} target="_blank" className="tb_wt_social_action__ico_wrap" rel="noopener noreferrer nofollow" aria-label={`Post comment Url ${NetworkComment(networkID, itemData.postId, itemData.link)}`} title="Comment">
            <div className="tb_wt_social_action_ico__ tb__icon tb-comment"> </div>
            {itemData.like_count > 0 || itemData.comment_count > 0 ? <div className="tb_wt_social_action_counts__">{itemData.like_count}</div> : ''}
          </a>
        </div>
        {networkID === 1 ?
          <div className="tb_wt_social_action__list">
            <a href={`https://twitter.com/intent/retweet?tweet_id=${convertToAtob(itemData.postId)}`} target="_blank" className="tb_wt_social_action__ico_wrap" rel="noopener noreferrer nofollow" aria-label={`Post comment Url https://twitter.com/intent/retweet?tweet_id=${convertToAtob(itemData.postId)}`} title="Tweet">
              <div className="tb_wt_social_action_ico__ tb__icon tb-retweet"> </div>
            </a>
          </div> : ''}
      </div>
      : ''}
  </>
}

export default WidgetSocialActions